import React from 'react'
import ChatBot from 'react-simple-chatbot'
import './bot.css'

import Filter from '../filter/filter.container'

class Bot extends React.Component {
    defaultSteps = [
        {
            id: '0',
            message: 'Bienvenue dans Mon entourage !',
            trigger: 1
        },
        {
            id: 1,
            message: `Une webapp non-officielle pour le réseau social solidaire Entourage`,
            trigger: 2
        },
        {
            id: 2,
            message: `Tu pourras trouver ici différentes façons de t'impliquer et d'aider les gens dans le besoin.`,
            trigger: 3
        },
        {
            id: 3,
            options: [
                { value: 1, label: `Voir uniquement les demandes d'aides`, trigger: 4 },
                { value: 2, label: `Voir uniquement les contributions`, trigger: 5 },
                { value: 3, label: 'Voir tout', trigger: 6 },
              ],
        },
        {
            id: 4,
            component: <Filter filterType='ask_for_help' />, 
            waitAction: true,
            asMessage: true,
            trigger: 3
        },
        {
            id: 5,
            component: <Filter filterType='contribution' />, 
            waitAction: true,
            asMessage: true,
            trigger: 3
        },
        {
            id: 6,
            component: <Filter filterType='none' />, 
            waitAction: true,
            asMessage: true, 
            trigger: 3
        }
    ]

    defaultConfig = {
        inputStyle: {
            display: 'none'
        },
        hideSubmitButton: true,
        botDelay: 2000,
        headerTitle: 'Mon entourage',
        botAvatar: `data:image/jpeg;base64,`   
    }


    async componentDidMount() {
		try {
            await this.props.createInstance()
            await this.props.getFeeds()
		} catch (err) {
			console.log(err)
		}
    }

    renderChatbot = () => (<ChatBot steps={this.defaultSteps} {...this.defaultConfig} className='Bot' />);

    render() {
        const { isLogged } = this.props
        return (
            <div className="Bot">
            {isLogged && this.renderChatbot()}
            </div>
        )
    }
}

export default Bot